class Pages{
    title:string ="";
    private imgsrc: any;
    private description:string = "";
    private genru:string = ""
    model:boolean = false;

    constructor(title:string, imgsrc:any, des:string, genru:string){
        this.title = title;
        this.imgsrc = imgsrc;
        this.description = des;
        this.genru = genru;
    }
    getTitle(){
        return this.title;
    }
    getGenru(){
        return this.genru;
    }
    getImgsrc(){
        return this.imgsrc;
    }
    getComment(){
        return this.description;
    }
    changeModel(){
        this.model = !this.model;
        console.log(this.model)
    }
}
